import { render } from 'solid-js/web';
import WebAuthnAuth, { type WebAuthnAuthProps } from './WebAuthnAuth';

// Define the custom element
class WebAuthnAuthElement extends HTMLElement {
  private disposeFunction?: () => void;

  static get observedAttributes(): string[] {
    return ['base-url', 'theme', 'class'];
  }

  connectedCallback(): void {
    this.render();
  }

  disconnectedCallback(): void {
    if (this.disposeFunction) {
      this.disposeFunction();
    }
  }

  attributeChangedCallback(): void {
    if (this.isConnected) {
      this.render();
    }
  }

  private render(): void {
    // Clean up previous render
    if (this.disposeFunction) {
      this.disposeFunction();
    }

    // Get attributes
    const baseUrl = this.getAttribute('base-url') || undefined;
    const theme =
      (this.getAttribute('theme') as WebAuthnAuthProps['theme']) || 'auto';
    const className = this.getAttribute('class') || undefined;

    // Create props
    const props: WebAuthnAuthProps = {
      ...(baseUrl && { baseUrl }),
      theme,
      ...(className && { className }),
      onLogin: (username: string) => {
        this.dispatchEvent(
          new CustomEvent('webauthn-login', {
            detail: { username },
            bubbles: true,
          })
        );
      },
      onLogout: () => {
        this.dispatchEvent(
          new CustomEvent('webauthn-logout', {
            bubbles: true,
          })
        );
      },
      onError: (error: string) => {
        this.dispatchEvent(
          new CustomEvent('webauthn-error', {
            detail: { error },
            bubbles: true,
          })
        );
      },
    };

    // Render the Solid component
    this.disposeFunction = render(() => WebAuthnAuth(props), this);
  }
}

// Register the custom element
if (!customElements.get('webauthn-auth')) {
  customElements.define('webauthn-auth', WebAuthnAuthElement);
}

// Export types and components for programmatic use
export { WebAuthnAuth as default, type WebAuthnAuthProps };
export { WebAuthnAuthElement };

// Export for direct usage without custom elements
export const createWebAuthnAuth = (
  container: Element,
  props: WebAuthnAuthProps
): (() => void) => {
  return render(() => WebAuthnAuth(props), container);
};

// Version info
export const VERSION = '1.0.0';

// Usage examples in JSDoc
/**
 * @example
 * HTML usage:
 * ```html
 * <webauthn-auth
 *   base-url="http://localhost:8080"
 *   theme="light"
 *   class="my-auth-component">
 * </webauthn-auth>
 *
 * <script>
 *   document.querySelector('webauthn-auth').addEventListener('webauthn-login', (e) => {
 *     console.log('User logged in:', e.detail.username);
 *   });
 * </script>
 * ```
 *
 * @example
 * Programmatic usage:
 * ```typescript
 * import { createWebAuthnAuth } from '@webauthn/web-component';
 *
 * const dispose = createWebAuthnAuth(document.getElementById('auth-container'), {
 *   baseUrl: 'http://localhost:8080',
 *   theme: 'dark',
 *   onLogin: (username) => console.log('Logged in:', username),
 *   onLogout: () => console.log('Logged out'),
 *   onError: (error) => console.error('Auth error:', error),
 * });
 *
 * // Later, cleanup
 * dispose();
 * ```
 */
